import React, { Component } from "react";
import Axios from "axios";
import toastr from 'toastr';
import Navbar from '../../components/NavBar/NavBar';
import Spinner from 'react-bootstrap/Spinner';
import './register.css';

const emailRegexp = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,4})+$/
const passRegexp = /^(?=\w*\d)(?=\w*[A-Z])(?=\w*[a-z])\S{8,16}$/

export class Register extends Component {

  state = {
    loadingSubmit: false,
    name: '',
    email: '',
    password: '',
    rpassword: ''
  };

  handleSubmit = (e) => {
    e.preventDefault();
    let error = false;
    this.setState({loadingSubmit: true})

    if(this.state.name === '' || this.state.email === '' || this.state.password === '' || this.state.rpassword === ''){
      toastr.remove()
      toastr.warning("Please complete all the fields");
      this.setState({loadingSubmit: false})
      error = true;
    }

    if(!this.state.email.match(emailRegexp)){
      toastr.remove()
      toastr.warning("Invalid Email");
      this.setState({loadingSubmit: false})
      error = true;
    }

    if(this.state.password !== this.state.rpassword){
      toastr.remove()
      toastr.warning("Passwords does not match");
      this.setState({loadingSubmit: false})
      error = true;
    }

    if(!this.state.password.match(passRegexp)){
      toastr.remove()
      toastr.warning("Passwords must have 8-16 characters with an uppercase letter and a number");
      this.setState({loadingSubmit: false})
      error = true;
    }

    if(!error){
      const data = {
        name: this.state.name,
        email: this.state.email,
        password: this.state.password,
      }
      Axios.post("/api/signup", data).then(response => {
        if(response.data.ok){
          this.setState({loadingSubmit: false})
          toastr.success('Account created!')
          setTimeout(() => {
            window.location = "/login"
          }, 2000);
        }else{
          toastr.remove()
          toastr.warning(response.data.error)
          this.setState({loadingSubmit: false})
        }
      }).catch(err=>{
        toastr.remove()
        toastr.warning(err.response.data.error)
        this.setState({loadingSubmit: false})
      });
    }
  }
  
  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  render() {
    return (
      <>
        <Navbar auth={this.props.auth} register={true}/>
        <div className="register-main">
          <div className="register-contenedor">
            <div className="row px-0 mx-0" style={{marginTop: '20vh'}}>
              <form className="border-shadow register-form" style={{width: '50%', marginLeft:'auto', marginRight: 'auto'}} onSubmit={this.handleSubmit}>
                <div className="row my-3">
                  <input onChange={this.handleChange} className="mx-auto w-75 my-2 form-input form-control" type="text" name="name" placeholder="Name"/>
                  <input onChange={this.handleChange} className="mx-auto w-75 my-2 form-input form-control" type="text" name="email" placeholder="E-mail"/>
                  <input onChange={this.handleChange} className="mx-auto w-75 my-2 form-input form-control" type="password" name="password" placeholder="Password"/>
                  <input onChange={this.handleChange} className="mx-auto w-75 my-2 form-input form-control" type="password" name="rpassword" placeholder="Verify Password"/>
                </div>
                <div className="row my-2">
                  <button className="col-4 btn btn-md mx-auto register-button" type="submit">
                    {this.state.loadingSubmit ? 
                      <Spinner animation="border" size='sm'/>
                    : 
                      'Create Account'
                    }
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Register;